/* ----------FONTS---------- */
@font-face {
    font-family: Roboto;
    src: local("Roboto-Regular"),
    url("../asset/fonts/Roboto-Regular.ttf") format("truetype");
}

@font-face {
    font-family: Futura;
    src: local("Futura-Medium-bt"),
    url("../asset/fonts/Futura-Medium-bt.ttf") format("truetype");
}

@font-face {
    font-family: Arcade;
    src: local("Arcade"),
    url("../asset/fonts/Arcade.TTF") format("truetype");
}

@font-face {
    font-family: Digital;
    src: local("Digital"),
    url("../asset/fonts/Digital-Mono.ttf") format("truetype");
}

@font-face {
    font-family: Merch;
    src: local("Merch Copy"),
    url("../asset/fonts/Digital-Monospaced.ttf") format("truetype");
}





/* ----------CONTENT---------- */
.Input {
    height: 55%;
    font-family: Futura;
    color: black;
    background-color:rgb(29, 29, 32);
}

.main-input {
    background-image: linear-gradient(to bottom right, rgb(237, 230, 231), rgba(230, 214, 225, 0.559));
    padding: 30px;
    margin-top: 30px;
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 30px;
    border-radius: 30px;
    width: 100%;
}

#input-tittle {
    margin-bottom: 20px;
    font-family: Futura;
    font-size: large;
}

.numbers-counter {
    display: block;
    border-radius: 10px;
    width: 100%;
    text-align: center;
    background-color: rgb(5, 7, 54);
    color: aquamarine;
    font-family: Digital;
    font-size: 50px;
}

.buttons-counter {
    margin-top: 10px;
}

.icon-counter {
    height: 50px;
    padding: 15px;
}

.btn-add {
    background-color: black;
    margin: 5px;
    border-radius: 3px;
}

.icon-add {
    height: 30px;
    padding: 5px;
}

.btn-reset {
    margin: 5px;
    color: black;
    border-color: black;
    border-radius: 3px;
    padding: 5px;
    border-width: 3px;
    margin-top: 35px;
}

.th-table-nama {
    padding-right: 100px;
}

.th-table-add {
    padding-right: 50px;
}

.th-table-del {
    padding-right: 30px;
}

.table-input {
    font-size: small;
}

.table-data-names-input{
    max-height: 350px;
    overflow: auto;
    overflow-y: scroll;
}

.table-data {
    padding-top: 5px;
    padding-bottom: 5px;
}




/* ----------MEDIA ADJUST---------- */
@media only screen and (min-width: 500px) {
    .Input{
        height: 80%;
    }

    .numbers-counter {
        width: 95%;
        font-size: 60px;
    }

    .table-input {
        font-size: medium;
    }
} 

@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}

    .Input{
        height: 80%;
    }

    #input-tittle {
        font-size: x-large;
    }

    .icon-counter {
        height: 60px;
    }
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}

    .main-input {
        padding: 15px;
    }

    .numbers-counter {
        font-size: 80px;
    }

    .table-input {
        font-size: large;
    }
}
  
@media only screen and (min-width: 980px) {
    .numbers-counter {
        width: 50%;
    }
} 

@media only screen and (max-height: 500px) {
    .Input{
        height: 100%;
        padding-bottom: 20px;
    }
}